import {useContext, useState} from "react";
import {AuthContext} from "../context/AuthContext";
import {CerberusContext} from "@a11n-io/cerberus-reactjs";

export default function useFetch(baseUrl) {
    const [loading, setLoading] = useState(false);
    const authCtx = useContext(AuthContext)
    const cerberusCtx = useContext(CerberusContext)

    const defaultHeaders = {
        "Content-Type": "application/json"
    }

    let hdrs = defaultHeaders
    if (cerberusCtx.apiTokenPair) {
        hdrs = { ...hdrs, Authorization: 'Bearer ' + cerberusCtx.apiTokenPair.accessToken }
    }

    function get(url, headers) {
        hdrs = { ...hdrs, ...headers }

        return new Promise((resolve, reject) => {
            setLoading(true);
            fetch(`${process.env.REACT_APP_CERBERUS_WEB_HOST}${baseUrl}${url}`, {
                credentials: "include",
                method: "get",
                headers: hdrs
            })
                .then(async response => {
                    if (response.status === 401 || response.status === 403) {
                        console.log('logout')
                        authCtx.logout();
                    }
                    if (response.status >= 400) {
                        console.log('reject')
                        setLoading(false);
                        reject(await response.json());
                        return
                    }
                    console.log('return json')
                    return response.json()
                })
                .then((data) => {
                    console.log('data', data)
                    setLoading(false)
                    if (!data) {
                        data = []
                    }
                    resolve(data)
                })
                .catch(error => {
                    console.log('error', error)
                    setLoading(false);
                    if (error === null) {
                        reject('error occurred')
                    }
                    if (error.message !== null) {
                        reject(error.message)
                    }
                    reject(error)
                });
        });
    }

    function post(url, body, headers) {
        hdrs = { ...hdrs, ...headers }

        return new Promise((resolve, reject) => {
            setLoading(true);
            fetch(`${process.env.REACT_APP_CERBERUS_WEB_HOST}${baseUrl}${url}`, {
                credentials: "include",
                method: "post",
                headers: hdrs,
                body: JSON.stringify(body)
            })
                .then(async response => {
                    if (response.status === 401 || response.status === 403) {
                        console.log('logout')
                        authCtx.logout();
                    }
                    if (response.status >= 400) {
                        console.log('reject')
                        setLoading(false);
                        reject(await response.json());
                    }
                    console.log('return json')
                    return response.json()
                })
                .then((data) => {
                    console.log('data', data)
                    setLoading(false)
                    if (!data) {
                        data = []
                    }
                    resolve(data)
                })
                .catch(error => {
                    console.log('error', error)
                    setLoading(false);
                    if (error === null) {
                        reject('error occurred')
                    }
                    if (error.message !== null) {
                        reject(error.message)
                    }
                    reject(error)
                });
        });
    }

    function put(url, body, headers) {
        hdrs = { ...hdrs, ...headers }

        return new Promise((resolve, reject) => {
            setLoading(true);
            fetch(`${process.env.REACT_APP_CERBERUS_WEB_HOST}${baseUrl}${url}`, {
                credentials: "include",
                method: "put",
                headers: hdrs,
                body: JSON.stringify(body)
            })
                .then(async response => {
                    if (response.status === 401 || response.status === 403) {
                        console.log('logout')
                        authCtx.logout();
                    }
                    if (response.status >= 400) {
                        console.log('reject')
                        setLoading(false);
                        reject(await response.json());
                    }
                    console.log('return json')
                    return response.json()
                })
                .then((data) => {
                    console.log('data', data)
                    setLoading(false)
                    if (!data) {
                        data = []
                    }
                    resolve(data)
                })
                .catch(error => {
                    console.log('error', error)
                    setLoading(false);
                    if (error === null) {
                        reject('error occurred')
                    }
                    if (error.message !== null) {
                        reject(error.message)
                    }
                    reject(error)
                });
        });
    }

    function del(url, headers) {
        hdrs = { ...hdrs, ...headers }

        return new Promise((resolve, reject) => {
            setLoading(true);
            fetch(`${process.env.REACT_APP_CERBERUS_WEB_HOST}${baseUrl}${url}`, {
                credentials: "include",
                method: "delete",
                headers: hdrs
            })
                .then(async response => {
                    if (response.status === 401 || response.status === 403) {
                        console.log('logout')
                        authCtx.logout();
                    }
                    if (response.status >= 400) {
                        console.log('reject')
                        setLoading(false);
                        reject(await response.json());
                    }
                    console.log('return json')
                    return response.json()
                })
                .then((data) => {
                    console.log('data', data)
                    setLoading(false)
                    if (!data) {
                        data = []
                    }
                    resolve(data)
                })
                .catch(error => {
                    console.log('error', error)
                    setLoading(false);
                    if (error === null) {
                        reject('error occurred')
                    }
                    if (error.message !== null) {
                        reject(error.message)
                    }
                    reject(error)
                });
        });
    }

    function postFiles(url, form) {
        return new Promise((resolve, reject) => {
            setLoading(true);
            fetch(`${process.env.REACT_APP_CERBERUS_WEB_HOST}${baseUrl}${url}`, {
                credentials: "include",
                method: "post",
                headers: hdrs,
                body: form
            })
                .then(async response => {
                    if (response.status === 401 || response.status === 403) {
                        console.log('logout')
                        authCtx.logout();
                    }
                    if (response.status >= 400) {
                        console.log('reject')
                        setLoading(false);
                        reject(await response.json());
                    }
                    console.log('return json')
                    return response.json()
                })
                .then((data) => {
                    console.log('data', data)
                    setLoading(false)
                    if (!data) {
                        data = []
                    }
                    resolve(data)
                })
                .catch(error => {
                    console.log('error', error)
                    setLoading(false);
                    if (error === null) {
                        reject('error occurred')
                    }
                    if (error.message !== null) {
                        reject(error.message)
                    }
                    reject(error)
                });
        });
    }

    return { get, post, put, del, postFiles, loading };
};